* {
  margin: 0;
  font-family: 'Source Sans Pro', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
  padding: 2px 4px;
  font-size: 90%;
  color: #c7254e;
  background-color: #f9f2f4;
  border-radius: 4px;
}

.underline {
  width: 75px;
  height: 8px;

  background-color: #247dc6;
  margin-top: 0.5rem;
  margin-bottom: 1.5rem;
}

.circle {
  margin-top: 2rem;
  margin-left: 5rem;
}

.btn {
  background-color: #e7e7e7;
  color: black;
  border: none;
  border-radius: 0.25rem;
  padding: 16px 32px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  margin: 10px 2px;
  transition: 0.5s;
}

.btn:hover {
  background-color: #247dc6;
  color: white;
}

.hero {
  padding: 2em;
  color: #444444;
  background: #fff;
  display: flex;
  padding-top: 50px;
  padding-bottom: 50px;
  background-image: url("data:image/svg+xml,%3Csvg width='12' height='24' viewBox='0 0 12 24' xmlns='http://www.w3.org/2000/svg'%3E%3Cg fill='none' fill-rule='evenodd'%3E%3Cg fill='%23929fac' fill-opacity='0.1'%3E%3Cpath d='M2 0h2v12H2V0zm1 20c1.105 0 2-.895 2-2s-.895-2-2-2-2 .895-2 2 .895 2 2 2zM9 8c1.105 0 2-.895 2-2s-.895-2-2-2-2 .895-2 2 .895 2 2 2zm-1 4h2v12H8V12z'/%3E%3C/g%3E%3C/g%3E%3C/svg%3E");
}

.hero h1 {
  font-size: 4rem;
}

.intro {
  padding: 2rem;
  width: 35rem;
  margin-left: 75px;
}

.intro p {
  font-size: 1.5rem;
}

.intro .meta {
  font-size: 14px;
  margin-top: 2rem;
}

.icon-wrapper {
  display: flex;
}

.container {
  padding: 50px;
}

.card {
  width: 100px;
  height: 100px;
  background-color: #f8f9fa;
  border-radius: 0.25rem;
  margin: 1rem;
  color: #444444;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

.card:hover {
  color: #247dc6;
}

.card .gi {
  font-size: 2em;
  margin-bottom: 5px;
}

.icon-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
}

.icon-name {
  text-align: center;
  color: #222222;
  font-size: 14px;
}
